import Swiper from 'swiper/swiper-bundle';

import 'swiper/swiper-bundle.min.css';

new Swiper('.swiper', {
  loop: true,
  grabCursor: true,
  centeredSlides: true,
  resistance: false,
  // slidesPerView: 1,
  // spaceBetween: 100,
  speed: 700,
  threshold: 1,
  loopPreventsSliding: false,

  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
    // hideOnClick: true,
  },
  pagination: {
    el: '.swiper-pagination',
    type: 'bullets', // String with type of pagination. Can be 'bullets' | 'fraction' | 'progressbar' | 'custom'
    dynamicBullets: true,
    dynamicMainBullets: 2,
  },

  autoplay: {
    delay: 5000,
  },

  effect: 'coverflow', // 'slide' | 'fade' | 'cube' | 'coverflow' | 'flip' | 'creative' | 'cards'
  coverflowEffect: {
    rotate: 90,
    slideShadows: false,
    scale: 0.7,
  },
  zoom: {
    maxRatio: 4,
  },
});

